$font-stack: -apple-system, BlinkMacSystemFont, Roboto, "Open Sans", "Helvetica Neue", sans-serif;

$red:rgba(232, 71, 71, 0.4);
$orange:rgba(242, 153, 74, 0.4);
$yellow:rgba(242, 201, 76, 0.6);
$green:rgba(33, 150, 83, 0.4);
$blue:rgba(86, 204, 242, 0.4);
$blue-darker:rgba(47, 128, 237, 0.4);
$grey:rgba(216, 216, 216, 0.5);
$grey-darker:rgba(216, 216, 216, 0.5);
$dark-red:#db5341;

@font-face {
  font-weight: 400;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  src: url(../fonts/Roboto-Regular.ttf);
}

@font-face {
  font-weight: 400;
  font-family: "Helvetica", sans-serif;
  font-style: normal;
  src: url(../fonts/HelveticaRegular.ttf);
}

@font-face {
  font-weight: 700;
  font-family: "HelveticaBold", sans-serif;
  font-style: normal;
  src: url(../fonts/HelveticaBold.ttf);
}

a {
  color: #000;
  font-weight: 900;
  font-size: 16px;
  font-family: Helvetica, sans-serif;
  line-height: 24px;
  text-decoration: none;
}

h3 {
  margin: 0;
  padding: 0 12px 0 0;
  overflow: hidden;
  color: #000;
  font-size: 22px;
  font-family: Helvetica, sans-serif;
  line-height: 24px;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
}

.logo-pic {
  max-width: initial;
}

body {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: $font-stack;
  background-color: #f2f2f2;

  .bg-red {
    background-color: $red;
  }

  .bg-orange {
    background-color: $orange;
  }

  .bg-yellow {
    background-color: $yellow;
  }

  .bg-green {
    background-color: $green;
  }

  .bg-blue {
    background-color: $blue;
  }

  .bg-blue-darker {
    background-color: $blue-darker;
  }

  .bg-grey {
    background-color: $grey;
  }

  .bg-grey-darker {
    background-color: $grey-darker;
  }

  .bg-white {
    background-color: #fff;
  }

  .bg-dark-red {
    background-color: #db5341;
  }

  .container {
    img {
      //width: 20rem;
    }
  }
}

.wrapper {
  padding-left: 12px;
}

header {
  width: 100%;
  height: 64px;
  background-color: #fff;

  .logo {
    display: flex;
    flex-direction: row;
    width: 172px;
    height: 30px;

    .Ya {
      margin-right: 3px;
    }
  }

  .search-line {
    display: none;
  }

  .header-btns {
    display: none;
  }

  .container {
    display: flex;
    justify-content: space-between;
    margin-top: 18px;
    padding: 0 16px;

    .burger {
      margin-top: 6px;
    }
  }
}

main {
  display: grid;
  justify-content: center;
  padding: 0 16px;

  .marks {
    display: grid;
    grid-auto-flow: row dense;
    grid-gap: 17px;
    grid-template-columns: 1fr;
  }

  .mark {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    border: 1px solid rgb(218, 220, 224);
    border-radius: 16px;
  }

  .mark-main {
    height: 100%;
    margin: -1px;
    padding: 23px 0 0 23px;
    border: 1px solid rgb(218, 220, 224);
    border-radius: 16px;
  }

  .mark-main.no-border {
    border: none;
  }

  .mark-main.pic-only {
    padding: 0;
    border: none;
  }

  .pic-container {
    border: 1px transparent;
    border-radius: 16px;
  }

  .mark-main.warning {
    display: flex;
    height: auto;
    padding: 14px 0 14px 27px;
    color: #fff;
    font-size: 16px;
    font-family: Helvetica, sans-serif;
    line-height: 22px;
  }

  .warning-msg {
    padding-left: 14px;
    letter-spacing: 0.5px;
  }

  .color-icons {
    display: flex;
    justify-content: space-between;
    margin: 8px 0;
    overflow-x: scroll;

    p {
      display: none;
    }
  }

  .color-icon {
    width: 48px;
    min-width: 48px;
    height: 48px;
    min-height: 48px;
    margin-right: 16px;
    border-radius: 8px;
  }

  .checkbox {
    width: 18px;
    height: 18px;
    margin-right: 13px;
    background-color: #d9d9d9;
    border-radius: 3px;
  }

  .checkbox.checked {
    background-color: #fc0;
  }

  .mark-checked {
    padding-left: 3px;
    fill: #000;
  }

  .mark-text {
    margin-top: 12px;
    margin-left: 1px;
    padding-right: 12px;
    padding-bottom: 14px;
    white-space: pre-line;
  }

  .single-text {
    padding-top: 12px;
  }

  .mark-text-no-header {
    padding-top: 4px;
    padding-bottom: 16px;
  }

  .no-bottom-border {
    border-style: solid solid none solid;

    .mark-text {
      padding-top: 14px;
    }
  }

  .buy-items {
    padding-top: 16px;
    padding-bottom: 10px;
  }

  .buy-item {
    display: flex;
    margin-bottom: 14px;
  }

  .mark-footer {
    padding: 0;

    h3 {
      padding-top: 21px;
    }

    .buy-items {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: -12px;
      padding-top: 26px;
      padding-left: 12px;
      color: #000;
      font-size: 16px;
      font-family: Helvetica, sans-serif;
      line-height: 20px;
    }

    .buy-item {
      margin-bottom: 12px;
      padding-right: 10px;

      p {
        text-decoration: line-through;
      }
    }
  }

  .mark-footer.border {
    height: 100%;
    margin: -1px;
    border: 1px solid rgba(218, 220, 224, 0.5);
    border-radius: 16px;
  }

  .mark-footer.app {
    flex-direction: column;
    padding-right: 12px;
    padding-left: 12px;

    .link {
      display: flex;
      flex-direction: row;
      padding: 12px;
    }

    .link.border-bot {
      border-bottom: 1px solid #fff;
    }

    .app-link {
      margin-left: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
    }
  }

  .footer-images {
    display: flex;
    justify-content: space-between;
    padding: 12px 0 12px 12px;

    .img-container {
      img {
        margin-right: 24px;
      }
    }
  }

  .footer-options {
    display: flex;
    flex-flow: column;
    width: 100%;
    margin-bottom: 20px;

    .tags {
      display: flex;
      padding-top: 2px;
    }

    .tag {
      display: flex;
      margin-right: 12px;
      padding: 0 12px;
      color: #000;
      font-size: 14px;
      font-family: Helvetica, sans-serif;
      line-height: 22px;
      text-align: center;
      background: rgba(51, 51, 51, 0.1);
      border-radius: 16px;
    }

    .pen {
      margin-left: 12px;
    }

    .note-info {
      display: flex;
      justify-content: space-between;
      padding-top: 15px;
      padding-right: 24px;
      font-size: 16px;
      font-family: Helvetica, sans-serif;
      line-height: 20px;
      text-align: right;
    }
  }
}

.shrinker {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}

footer {
  width: 100%;
  height: 54px;
  margin-top: 17px;
  color: rgba(51, 51, 51, 0.6);
  font-size: 16px;
  font-family: Helvetica, sans-serif;
  line-height: 32px;
  background-color: #fff;

  .container {
    display: flex;
    justify-content: space-between;
    padding: 0 12px;
  }
}

@media screen and (min-width: 769px) {
  button {
    font-size: 16px;
    font-family: Helvetica, sans-serif;
    line-height: 32px;
    text-align: center;
  }

  ::-webkit-input-placeholder {
    padding-left: 12px;
    font-size: 15px;
    font-family: Helvetica, sans-serif;
    line-height: 32px;
    opacity: 0.2;
  }

  ::-moz-placeholder {
    padding-left: 12px;
    font-size: 15px;
    font-family: Helvetica, sans-serif;
    line-height: 32px;
    opacity: 0.2;
  }

  :-moz-placeholder {
    padding-left: 12px;
    font-size: 15px;
    font-family: Helvetica, sans-serif;
    line-height: 32px;
    opacity: 0.2;
  }

  :-ms-input-placeholder {
    padding-left: 12px;
    font-size: 15px;
    font-family: Helvetica, sans-serif;
    line-height: 32px;
    opacity: 0.2;
  }

  header {
    .logo {
      height: 28px;
    }

    .burger {
      display: none;
    }

    .search-line {
      display: flex;
      justify-content: center;
      width: 100%;

      .input-container {
        position: relative;

        .close-icon {
          position: absolute;
          top: calc(50% - 6px);
          right: 8px;
        }
      }

      button {
        width: 72px;
        height: 32px;
        margin-left: -1px;
        background-color: #e6e6e6;
        border: none;
        border-radius: 0 4px 4px 0;
      }

      .find-input {
        box-sizing: border-box;
        width: 30vw;
        height: 32px;
        margin-left: 15px;
        border: 2px solid #e6e6e6;
        border-radius: 4px;
      }
    }

    .header-btns {
      display: flex;

      button {
        margin-right: 5px;
        padding: 0 10px;
        color: #333;
        background-color: #fff;
        border: none;
        cursor: pointer;
      }
      .add-btn{
        background-color: #fc0;
        border: none;
        border-radius: 4px;
      }
      .arch-btn{
        opacity:0.5;
      }
    }
  }

  main {
    grid-gap: 32px;
    max-width: 1020px;

    .color-icons {
      justify-content: normal;
      align-items: center;
      margin-top: 32px;
      overflow-x: hidden;

      p {
        display: block;
        margin-right: 16px;
        font-size: 36px;
        font-family: Helvetica, sans-serif;
        line-height: 42px;
      }
    }

    .color-icon {
      width: 32px;
      height: 32px;
      margin-right: 16px;
      cursor: pointer;
    }

    .marks {
      grid-auto-flow: row;
      grid-gap: 24px;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    }

    .notes-control {
      visibility: hidden;
    }

    .big {
      grid-column: span 4;
    }

    .medium {
      grid-column: span 3;
    }

    .small {
      grid-column: span 2;
    }

    .height-1x {
      grid-row: span 1;
    }

    .height-2x {
      grid-row: span 2;
    }

    .height-3x {
      grid-row: span 3;
    }
  }
}

@media screen and (min-width: 1110px) {
  .header-btns {
    button {
      margin-right: 32px;
      padding: 0 15px;
    }
  }

  .search-line {
    button {
      width: 76px;
    }

    .find-input {
      max-width: 400px;
    }
  }
}
